import React, {
  useContext,
  useState,
  useEffect,
} from "react";
import "./userbox.scss";
import {
  Brightness4,
  Brightness7,
} from "@material-ui/icons";

import { AuthContext } from "../../context/AuthContext";

import { Link } from "react-router-dom";
import API from "../../serivce/API";

export default function Userbox() {
  const RF = process.env.REACT_APP_PUBLIC_FOLDER;

  const [theme, setTheme] = useState(false);
  const changeTheme = () => {
    setTheme(!theme);
  };
  const { user: currentUser } = useContext(AuthContext);

  const [user, setUser] = useState({});
  useEffect(() => {
    const fetchUser = async () => {
      const res = await API().get(
        `users/${currentUser._id}`
      );
      setUser(res.data);
    };
    fetchUser();
  }, [currentUser, setUser]);
  return (
    <div className='box'>
      <div className='header'>
        <Link to={user ? `/profile/${user.username}` : "/"}>
          <img
            src={
              RF + user.profilePicture ||
              RF + "person/noAvatar.png"
            }
            className='userAvatar'
            alt='avatart'
          />
        </Link>

        <div>{user.username}</div>
        {theme ? (
          <span className='dark' onClick={changeTheme}>
            <Brightness7 />
          </span>
        ) : (
          <span className='light' onClick={changeTheme}>
            <Brightness4 />
          </span>
        )}
      </div>
      <Link to='/portfolio'>我的作品集</Link>
      <Link to='/filter'>我的滤镜</Link>
    </div>
  );
}
