import React, {
  useContext,
  useState,
  useEffect,
} from "react";
import { Formik, Field, Form } from "formik";

import Layout from "../../components/layout/Layout";
import "./setting.scss";
import { Button } from "@material-ui/core";
import { AuthContext } from "../../context/AuthContext";
import API from "../../serivce/API";
import Userbox from "../../components/userbox/Userbox";
import { Settings } from "@material-ui/icons";
export default function Setting() {
  const { user: currentUser } = useContext(AuthContext);
  const RF = process.env.REACT_APP_PUBLIC_FOLDER;
  const [file, setFile] = useState(null);
  const [user, setUser] = useState({});
  const { username, city, from, desc } = user;
  useEffect(() => {
    const fetchUser = async () => {
      const res = await API().get(
        `users/${currentUser._id}`
      );
      setUser(res.data);
    };
    fetchUser();
  }, [currentUser, setUser]);
  const handlerSubmit = (value) => {
    //更新完了。怎么重新获取用户数据？

    const newUser = {
      userId: currentUser?._id,
      ...value,
    };

    const updateUser = async () => {
      try {
        if (file) {
          const data = new FormData();
          const fileName = Date.now() + file.name;

          data.append("fileName", fileName);
          data.append("file", file);
          newUser.profilePicture = fileName;
          await API().post("/upload", data);
        }
        await API().put(
          `users/${currentUser._id}`,
          newUser
        );
        alert("保存成功");
      } catch (error) {
        console.log(error);
      }
    };
    updateUser();
  };

  const Basic = () => (
    <div className='form'>
      <div className='formWrapper'>
        <h2 className='heading'>
          <Settings fontSize='large'></Settings>
          设置
        </h2>
        <label htmlFor='file'>
          {file ? (
            <img
              alt=''
              className='userImg'
              src={URL.createObjectURL(file)}
            />
          ) : (
            <img
              src={
                RF + user.profilePicture ||
                RF + "person/noAvatar.png"
              }
              alt=''
              className='userImg'
            />
          )}

          <input
            type='file'
            id='file'
            style={{ display: "none" }}
            accept='.png,.jpeg,.jpg'
            onChange={(e) => setFile(e.target.files[0])}
          />
        </label>
        <Formik
          initialValues={{ username, city, from, desc }}
          onSubmit={handlerSubmit}
        >
          <Form className='formBasic'>
            <label htmlFor='username'>用户名字</label>
            <Field
              id='username'
              name='username'
              autoComplete='off'
              className='fieldItem'
            />

            <label htmlFor='desc'>简介</label>
            <Field
              id='desc'
              name='desc'
              autoComplete='off'
              className='fieldItem'
            />
            <label htmlFor='city'>城市</label>
            <Field
              id='city'
              name='city'
              autoComplete='off'
              className='fieldItem'
            />

            <label htmlFor='from'>家乡</label>
            <Field
              id='from'
              name='from'
              autoComplete='off'
              className='fieldItem'
            />
            <Button
              variant='contained'
              color='primary'
              type='submit'
            >
              保存设置
            </Button>
          </Form>
        </Formik>
      </div>
    </div>
  );
  return (
    <Layout>
      <div className='sidebar'></div>
      <div className='center'>
        <Basic />
      </div>
      <div className='rightbar'>
        <Userbox />
      </div>
    </Layout>
  );
}
