import "./topbar.css";
import {
  Person,
  Search,
  Settings,
  Home,
} from "@material-ui/icons";
import React, {
  useContext,
  useEffect,
  useState,
} from "react";
import { Link } from "react-router-dom";
import { AuthContext } from "../../context/AuthContext";
import Storage from "../../serivce/localStorage";
import API from "../../serivce/API";

export default function Topbar() {
  const RF = process.env.REACT_APP_PUBLIC_FOLDER;
  const { user: currentUser } = useContext(AuthContext);
  const [user, setUser] = useState({});

  useEffect(() => {
    const fetchUser = async () => {
      const res = await API().get(
        `users/${currentUser._id}`
      );
      setUser(res.data);
    };
    fetchUser();
  }, [currentUser, setUser]);

  const handlerLogout = () => {
    Storage.clear();
    window.location.reload();
  };
  return (
    <div className='topbarContainer'>
      <div className='topbarLeft'>
        <Link to='/'>
          <span className='logo'>Fujix</span>
        </Link>
      </div>
      <div className='topbarCenter'>
        <div className='searchbar'>
          <Search className='searchIcon' />
          <input
            type='text'
            placeholder='搜索好友或者有趣的事情'
            className='searchInput'
          />
        </div>
      </div>
      <div className='topbarRight'>
        <Link to={user ? `/profile/${user.username}` : "/"}>
          <img
            src={
              RF + user.profilePicture ||
              RF + "person/noAvatar.png"
            }
            className='userAvatar'
            alt='avatart'
          />
        </Link>
        <Link to='/'>
          <span className='home'>
            <Home />
          </span>
        </Link>
        <Link to='/setting'>
          <span className='setting'>
            <Settings />
          </span>
        </Link>
        <span className='logout' onClick={handlerLogout}>
          登出
        </span>
      </div>
    </div>
  );
}
